import RiveCanvas, { File, Artboard, StateMachineInstance, LinearAnimationInstance, SMIInput } from "@rive-app/canvas-advanced";
// import "style.css";

interface TileInstanceData {
  x: number;
  y: number;
  artboard: Artboard;
  machine: StateMachineInstance;
  anim: LinearAnimationInstance;
  anim2: LinearAnimationInstance;
  tapTrigger: SMIInput;
}

async function main() {
  const rive = await RiveCanvas({
    // Loads Wasm bundle
    locateFile: (_) =>
      `https://unpkg.com/@rive-app/canvas-advanced@2.2.0/rive.wasm`
  });
  const canvas = document.getElementById("rive-canvas") as HTMLCanvasElement;
  canvas.height = document.documentElement.clientHeight;
  canvas.width = document.documentElement.clientWidth;

  const scaleX = canvas.width / 1792;
  const scaleY = canvas.height / 922;

  const padX = 10 * scaleX;
  const padY = 10 * scaleY;

  const dim = (canvas.height - padY * 6) / 5;
  const bgWidth = canvas.width - (padX + dim) * 2;
  // const bgWidth = canvas.width;
  
  const renderer = rive.makeRenderer(canvas);
  const bytes = await (
    await fetch(new Request("2023-10-23-tile_test-001.riv"))
    // await fetch(new Request("Button.riv"))
  ).arrayBuffer();
  const file = (await rive.load(new Uint8Array(bytes))) as File;
  const artboardG = file.defaultArtboard();

  const bytes2 = await (
    await fetch(new Request("Background.riv"))
    // await fetch(new Request("2023-10-26-Background-Test-001.riv"))
    // await fetch(new Request("2023-10-25-Background-Test-001.riv"))
    // await fetch(new Request("2023-10-23-Background-Test-001.riv"))
  ).arrayBuffer();
  const file2 = (await rive.load(new Uint8Array(bytes2))) as File;
  const artboardBG = file2.defaultArtboard();

  let bgAnim = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(0),
    artboardBG.animationByName("Tap"),
    artboardBG
  );
  
  let bgAnimPlane = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(0),
    artboardBG.animationByName("Plane Flying"),
    artboardBG
  );

  const bgMachine = new rive.StateMachineInstance(
    artboardBG.stateMachineByName("State Machine"),
    artboardBG
  );

  const progressDuration = 2;

  // console.log(artboardBG);

  for (let i = 0; i < artboardBG.animationCount(); ++i) {
      console.log(artboardBG.animationByIndex(i).name);
  }
  // console.log(bgAnim);
  let progressT = 0;
  let bgMachineProgress;
  let bgMachineTileSide;
  let bgMachineBalloonX;
  let bgMachineTap;
  let balloonDirection = 1;
  let balloonX = 0;
  for (let i = 0, l = bgMachine.inputCount(); i < l; i++) {
    const input = bgMachine.input(i);
    console.log(input.name, input.type)
    // console.log(input, input.name, input.type, input.asNumber().value)
    if (input.name == "Progress") {
      bgMachineProgress = input;
    } else if (input.name == "TileSide") {
      bgMachineTileSide = input;
    } else if (input.name == "BalloonX") {
      bgMachineBalloonX = input;
    } else if (input.name == "Tap") {
      bgMachineTap = input;
      // console.log(bgMachineTap)
    }
      
  }

  const tileNums = [] as any;
  const tileNumsT = [] as any;
  var currTile = -1;
  var currNum = 1;
  var topNum = 10;
  var isCorrect = false;
  var tileAnim = [] as any;

  for (let i = 0; i < 10; ++i) {
    tileNums[i] = i + 1;
    tileNumsT[i] = tileNums[i];
    tileAnim[i] = {
      type: 0,
      t: 0
    };
  }

  shuffleArr(tileNums);

  let lastTime = 0;
  // console.log(artboard);
  const tile = file.artboardByName("artboard");
  const tiles = new Set<TileInstanceData>();
  // console.log(tile);
  spawnTiles();
  // spawnTiles();

  window.addEventListener('resize', (e) => {
    location.href = '';
  });

  let mDown = false;
  canvas.addEventListener('mousedown', (e) => {
    let mx = e.offsetX;
    let my = e.offsetY;

    if (!mDown) {
      mDown = true;
      currTile = isTileClicked(mx, my);

      if (currTile > -1) {
        if (tileAnim[currTile].t == 0) {
          if (currNum == tileNums[currTile]) {
            isCorrect = true;
            currNum++;
            
            // bgMachineTileSide.asNumber().value += 100;
            bgMachineTap.asTrigger().fire();
            // tileNums[currTile] = ++topNum;
            tileNumsT[currTile] = ++topNum;
            tileAnim[currTile].type = 1;
            tileAnim[currTile].t = 2;
            progressT = progressDuration;

            if (currTile < 5) {
              bgMachineTileSide.asNumber().value = 0;
              balloonDirection = 1;
            } else {
              bgMachineTileSide.asNumber().value = 1;
              balloonDirection = -1;
            }
          } else {
            isCorrect = false;
            tileAnim[currTile].type = 2;
            tileAnim[currTile].t = 2;
          }
        }
        
      }
    }
  });

  canvas.addEventListener('mouseup', (e) => {
    if (mDown) {
      mDown = false;
    }
  });

  function spawnTiles() {
    for (let i = 0; i < 2; ++i) {
      for (let j = 0; j < 5; ++j) {
        const tileInstance = file.artboardByName("artboard");
        const tileMachine = new rive.StateMachineInstance(
          tile.stateMachineByName("State Machine"),
          tileInstance
        );
        
        let tapTrigger;

        for (let i = 0, l = tileMachine.inputCount(); i < l; i++) {
          const input = tileMachine.input(i);
          // console.log(input.name)
          switch (input.name) {
            case "Tap":
              tapTrigger = input.asTrigger();
              break;
  
            default:
              break;
          }
        }
        let x = i ? canvas.width - (dim + padX) : padX;

        let anim = new rive.LinearAnimationInstance(
          tileInstance.animationByName("Tap / Correct"),
          tileInstance
        );

        let anim2 = new rive.LinearAnimationInstance(
          tileInstance.animationByName("Tap / Incorrect"),
          tileInstance
        );

        const blockInstance = {
          x: x,
          y: j * dim + padY * (j + 1),
          artboard: tileInstance,
          machine: tileMachine,
          anim: anim,
          anim2: anim2,
          tapTrigger: tapTrigger
        };
  
        tileMachine.advance(0);
        blockInstance.artboard.advance(0);
  
        tiles.add(blockInstance);
      }
    }
  }

  function isTileClicked(mx, my) {
    for (let i = 0; i < 2; ++i) {
      for (let j = 0; j < 5; ++j) {
        let x = i ? canvas.width - (dim + padX) : padX;
        let y = j * dim + padY * (j + 1);
        if (mx >= x && mx <= x + dim && my >= y && my <= y + dim) {
          return i * 5 + j;
        }
      }
    }

    return -1;
  }

  function shuffleArr(array) {
    for (var i = array.length - 1; i > 0; i--) {
        var rand = Math.floor(Math.random() * (i + 1));
        [array[i], array[rand]] = [array[rand], array[i]]
    }
  }

  function renderLoop(time) {
    if (!lastTime) {
      lastTime = time;
    }
    const elapsedTimeMs = time - lastTime;
    const elapsedTimeSec = elapsedTimeMs / 1000;
    lastTime = time;

    renderer.clear();
    renderer.save();

    if (artboardG && artboardBG) {
      let idx = 0;

      if (bgAnimPlane) {
        bgAnimPlane.advance(elapsedTimeSec);
        bgAnimPlane.apply(1);
      }

      // for (let i = 0, l = bgMachine.inputCount(); i < l; i++) {
      //   const input = bgMachine.input(i);
        
      //   // console.log(input, input.name, input.type, input.asNumber().value)
      //   input.asNumber().value += elapsedTimeSec;
      // }
      // 
      if (progressT > 0) {
        progressT -= 1 * elapsedTimeSec;
        let val = 1 * progressT;
        bgMachineProgress.asNumber().value += val;
        
        balloonX += balloonDirection * val;
        if (Math.abs(balloonX) >= 100) {
          balloonX = 100 * balloonDirection;
        }
        bgMachineBalloonX.asNumber().value = balloonX;
        
        if (progressT <= 0) {
          progressT = 0;
        }
      }

      // bgMachineBalloonX.asNumber().value += 1000 * elapsedTimeSec;

      bgMachine.advance(elapsedTimeSec);

      renderer.save();
      let x = padX + dim;
      let y = 0;
      renderer.translate(x, y);
      renderer.align(
        rive.Fit.fitWidth,
        rive.Alignment.center,
        {
          minX: 0,
          minY: 0,
          maxX: bgWidth,
          maxY: canvas.height
        },
        artboardBG.bounds
      );
      artboardBG.advance(elapsedTimeSec);
      // artboardBG.apply(elapsedTimeSec);
      // artboardBG.apply(elapsedTimeSec);
      artboardBG.draw(renderer);
      renderer.restore();

      for (let blockInstance of Array.from(tiles)) {
        const { artboard, machine, anim, anim2, tapTrigger, x, y } = blockInstance;

        renderer.save();

        if (tileAnim[idx].t > 0) {
          if (tileAnim[idx].type == 1) {
            anim.advance(elapsedTimeSec);
            anim.apply(1);
          } else if (tileAnim[idx].type == 2) {
            anim2.advance(elapsedTimeSec);
            anim2.apply(1);
          }

          tileAnim[idx].t -= 1 * elapsedTimeSec;
          if (tileAnim[idx].t <= 0) {
            tileAnim[idx].t = 0;
            tileAnim[idx].type = 0;
            anim.time = 0;
            anim2.time = 0;
            
          } else if (tileAnim[idx].t <= 1) {
            if (tileAnim[idx].type == 1) {
              tileNums[idx] = tileNumsT[idx];
            }
          }
        }

        var textRun = artboard.textRun("Count");
        textRun.text = tileNums[idx].toString();
        // console.log(textRun.text)
        renderer.translate(x, y);
        renderer.align(
          rive.Fit.contain,
          rive.Alignment.center,
          {
            minX: 0,
            minY: 0,
            maxX: dim,
            maxY: dim
          },
          artboard.bounds
        );

        tapTrigger.fire();
        
        artboard.advance(elapsedTimeSec);
        artboard.draw(renderer);
        renderer.restore();
        idx++;
      }
    }

    renderer.restore();
    rive.requestAnimationFrame(renderLoop);
  }
  rive.requestAnimationFrame(renderLoop);
}

main();
